.popup {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	z-index: 15;
	@include m {
		overflow-y: scroll; }
	&__overlay {
		min-height: 100%;
		background: rgba(#000, 0.5);
		@include m {
			display: none; } }
	&__content {
		padding: 30px 20px 45px;
		@include vcenter;
		width: 400px;
		background-color: white;
		text-align: center;
		@include m {
			overflow-y: scroll;
			width: 100%;
			height: 100%;
			border: none;
			position: relative;
			top: 0;
			left: 0;
			transform: translate(0, 0);
			padding: 60px 20px 25px; } }
	&__close {
		position: absolute;
		width: 40px;
		height: 40px;
		background: $blue;
		top: 0;
		right: -50px;
		cursor: pointer;
		@include trans;
		color: $white;
		padding-top: 3px;
		font-size: 30px;
		@include m {
			right: 0;
			width: 40px;
			height: 40px; }

		&:before {
			content: '';
			@include vcenter;
 }			// +s(close)
		&:hover {
 } } }			// background: $orange3
