.news-list {
	padding-bottom: 20px; }
.news {
	display: block;
	margin-bottom: 50px;
	overflow: hidden;
	@include clr;
	color: $black;
	@include m {
		margin-bottom: 30px; }
	&__img {
		float: left;
		font-size: 0;
		max-width: 263px;
		max-height: 136px;
		height: auto;
		@include m {
			float: none;
			width: 100%;
			margin-bottom: 10px; } }
	&__descr {
		float: right;
		width: calc(100% - 316px);
		padding-top: 20px;
		@include m {
			float: none;
			width: 100%; } }
	&__date {
		margin-bottom: 15px;
		padding-left: 26px;
		position: relative;
		font-style: italic;
		@include m {
			margin-left: 22px; }
		.decor-lines {
			position: absolute;
			top: 5px;
			left: 0;
			transform: translateX(-50%); } }
	.styled-text {
		&:before {
			bottom: auto;
			height: 300px; } } }
